main{
    //xbackground-color: #77a9ca !important;
}
div.error404-block{
    height: 100%;
    background-color: #77a9ca;
    color: #46515d;
    text-align: right;
    font-size: 1.5em;
    font-family: ProximaNovaLight;
    padding: 3em 9%;
    h1{
        font-size: 2em;
        font-family: SerifGoticBlack;
        text-align: left;
        margin: 0;
        line-height: 1em;
    }
    div.error-body{
        padding: 0 2%;
        img{
            display: inline-block;
            width: 47%;
            vertical-align: bottom;
            margin-top: -2em;
            height: auto;
        }
        div.message{
            display: inline-block;
            width: 51%;
            p{
                text-align: center;
                margin: 0;
                a{
                    color: #9a564e;
                    font-family: ProximaNovaSemibold;
                }
            }
        }
    }
}

@mixin page404-max-width-1100 {
    div.error404-block{
        font-size: 1.3em;
    }
}

@mixin page404-max-width-990 {
    div.error404-block{
        font-size: 1.1em;
    }
}

@mixin page404-max-width-790 {
    div.error404-block{
        padding: 3em 4%;
        div.error-body{
            img{
                margin-top: -1em;
            }
        }
    }
}

@mixin page404-max-width-665 {
    div.error404-block{
        font-size: 0.9em;
    }
}

@mixin page404-max-width-399 {
    div.error404-block{
        font-size: 0.7em;
    }
}